@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  color: #000;
  background: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 400px;
  height: 250px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.battery {
  width: 300px;
  height: 150px;
  background-color: #fff;
  border: 5px solid #000;
  border-radius: 10px;
  place-content: center;
  display: grid;
  position: relative;
}

.battery:after {
  content: "";
  width: 20px;
  height: 50px;
  background-color: #000;
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 45px;
  right: -20px;
}

.charging {
  width: 270px;
  height: 120px;
  background-color: #000;
  border-radius: 5px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.charging-percent {
  color: red;
  z-index: 100;
  font-size: 40px;
  font-weight: 600;
}

.charging-time-remaining {
  font-size: 20px;
  font-weight: 500;
}

.charging-time-remaining p {
  text-align: center;
  color: red;
}

.charging.active {
  animation: 1.5s linear infinite percentage;
}

@keyframes percentage {
  0% {
    width: 0;
  }

  100% {
    width: 270px;
  }
}

